@import url('https://fonts.googleapis.com/css2?family=Miriam+Libre&display=swap');*/

body {
  margin: 0;
  /* font-family: 'Inter', sans-serif; 
  font-family: 'Miriam Libre', sans-serif;*/
  font-family: 'IBM Plex Sans', sans-serif;
  font-weight:bold;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  cursor:default;
  /* background-color: #a1c3d1; */
  /*background-image:linear-gradient(to top, rgba(23, 32, 42, 0.20), rgba(0, 0, 0, 0.80)), url("https://picsum.photos/1920/1080?random=6";*/
  background-size: cover;
  background-position:center;
  background-repeat: no-repeat;
  background-attachment: fixed;
}

/* .vanta-canvas {
  width:100% !important;
  position: fixed !important;
} */

#bg{
  /*height and width are important*/
  width:100%;
  height:100vh; 
}

#titleandlogo{
  margin:auto;
  margin-top:60px;
  margin-bottom:75px;
  display:flex;
  width:90vw;
  align-items: center;
}

#titleandlogo #title{
  color:white;
  font-size:1rem;
  margin-left:10px;
  letter-spacing: .5rem;
}

#titleandlogo #logo{
  height:40px;
}

#titleandlogo #time{
  font-size:0.8rem;
  margin:auto;
  margin-right:0;
  color:white;
  /* padding:2px 10px; */
}

#titleandlogo #options-box{
  display:flex;
  align-items:center;
  margin-left:15px;
}

#titleandlogo #options-box img{
  cursor:pointer;
  object-fit:cover;
  border-radius:50%;
  height:45px;
}

#titleandlogo #options-box #options-list{
  background-color:white;
  position: absolute;
  right:4.5%;
  top:95px;
  border-radius:8px;
  overflow: hidden;
}

#titleandlogo #options-box #options-list div:nth-child(1){
  padding:15px;
}

#titleandlogo #options-box #options-list div:nth-child(1):hover{
  background-color:#eee;
  cursor:pointer;
  transition:all .25s ease-in-out;
}

#titleandlogo #options-box #options-list #logout-button{
  cursor:pointer;
  width:100%;
  padding:15px;
  box-sizing: border-box;
  background-color:#658a99;
  color:white;
  text-align: center;
}

#body{
  width:85vw;
  margin:auto;
  margin-bottom:50px;
}


#body #widget-grid{
  display:grid;
  grid-template-columns:1fr 1fr;
  grid-gap:1rem;
}

#body #left-1,#body #right-1{
  height:-webkit-fit-content;
  height:-moz-fit-content;
  height:fit-content;
  display:flex;
  flex-direction: column;
}


#welcome{
    height:-webkit-fit-content;
    height:-moz-fit-content;
    height:fit-content;
    width:-webkit-fit-content;
    width:-moz-fit-content;
    width:fit-content;
    margin:auto;
    margin-bottom:50px;
    border:2px solid white;
    padding:25px;
    color:white;
    text-align:center;
    font-size:2.8rem;
}
#notes{
    /* background-color:#658a99; */
    background-color:white;
    padding:10px 15px;
    border-radius:8px;
    color:black;
    height:-webkit-fit-content;
    height:-moz-fit-content;
    height:fit-content;
}

#loading{
    padding:15px;
    background-color:white;
    display:flex;
    border-radius:8px;
    align-items:center;
    justify-content: center;
}

.note{
    background-color:#e6edf0;
    padding:10px;
    margin:10px 5px;
    border-radius:8px;
    padding-bottom:15px;
}

.note-title{
    padding:8px;
    width:100%;
    max-height:4rem;
    font-size:1.2rem;
    box-sizing:border-box;
    outline:none;
    resize: none;
    font-family: 'IBM Plex Sans', sans-serif;
    font-size:1.2rem;
    color:#658a99;
    border:none;
    background-color:#e6edf0;
    font-weight: bold;
}

/* .note .note-title:focus{
    background-color:white;
} */

.note-body{
    display:block;
    margin:auto;
    width:100%;
    padding:8px;
    box-sizing:border-box;
}

.note-textarea{
    padding:8px;
    width:100%;
    background-color:white;
    border:none;
    max-height:7rem;
    box-sizing:border-box;
    outline:none;
    resize: none;
    font-family: 'IBM Plex Sans', sans-serif;
    font-size:1rem;
}

.buttons{
    display:flex;
    justify-content:flex-end;
    align-items:center;
    box-sizing: border-box;
    margin:auto;
}

.note .buttons .update-status{
    margin:auto;
    margin-left:8px;
    font-size:0.9rem;
    padding:8px 0;
    width:-webkit-fit-content;
    width:-moz-fit-content;
    width:fit-content;
    display:flex;
}

.note .buttons .save-button, #create-note-button, .note .buttons .delete-button{
    background-color:#658a99;
    padding:8px;
    color:white;
    cursor:pointer;
    width:-webkit-fit-content;
    width:-moz-fit-content;
    width:fit-content;
    display:flex;
    align-items:center;
    margin-right:8px;
    border:2px solid #658a99;
}

.note .buttons .delete-button{
    background:transparent;
    border:2px solid #4e7686;
    color:#4e7686;
}

#notes #create-new-note{
    width:98.5%;
    padding:12px;
    margin:10px auto;
    /* border:2px solid white; */
    border-radius: 8px;
    cursor:pointer;
    text-align:center;
    color:white;
    box-sizing:border-box; 
    background-color:#4e7686;
}

#notes #create-new-note-box{
    background-color:#bed3db;
    padding:10px;
    padding-bottom:15px;
    margin:10px 5px;
    border-radius:8px;
}

#create-new-note-box .note-title{
    background-color:#bed3db;
}

#create-new-note-box .note-title::-webkit-input-placeholder{
    color:white;
}

#create-new-note-box .note-title:-ms-input-placeholder{
    color:white;
}

#create-new-note-box .note-title::-ms-input-placeholder{
    color:white;
}

#create-new-note-box .note-title::placeholder{
    color:white;
}

#quote{
    margin:50px auto;
    width:100%;
    padding:10px;
    box-sizing: border-box;
    text-align:center;
    color:white;
}

#quote #content{
    width:90%;
    line-height:2rem;
    font-style:italic;
    font-size:1.2rem;
    display:block;
    margin:auto;
}

#quote #line{
    width:150px;
    height:1px;
    margin:20px auto;
    background-color:white;
}

#quote #author{
    display:block;
    margin-top:5px;
}
#weather{
    padding:20px;
    background-color:white;
    height:-webkit-fit-content;
    height:-moz-fit-content;
    height:fit-content;
    width:100%;
    box-sizing: border-box;
    color:black;
    border-radius:8px;
}

#weather #temperature{
    font-size:3rem;
}
#pictures-box{
    overflow:auto;
    padding:20px;
    border-radius:8px;
    background-color:white;
    margin-top:1rem;
    display:flex;
    padding-bottom:40px;
    flex-direction:column;
    align-items: center;
    justify-content:center;
}

#pictures-box #title{
    margin:auto;
    margin-left:0;
    font-size:1rem;
    margin-bottom:30px;
}

#pictures-box .img-holder{
    width:500px;
    margin:5px auto;
    height:275px;
    object-fit:cover;
}
#dictionary{
    padding:20px;
    background-color:#e6edf0;
    border-radius:8px;
    color:#4e7686;
    margin-top:1rem;
}

#dictionary #search{
    margin-top:20px;
}

#dictionary #search input{
    padding:8px;
    width:100%;
    background-color:white;
    border:none;
    box-sizing:border-box;
    outline:none;
    font-family: 'IBM Plex Sans', sans-serif;
    font-size:1rem;
}

.buttons{
    display:flex;
    justify-content:flex-end;
    box-sizing: border-box;
    margin:auto;
}

.buttons #search-button{
    background-color:#658a99;
    padding:8px;
    font-family: 'IBM Plex Sans', sans-serif;
    font-size:1rem;
    font-weight: bold;
    color:white;
    cursor:pointer;
    width:-webkit-fit-content;
    width:-moz-fit-content;
    width:fit-content;
    display:flex;
    border:none;
    border-radius:0;
    align-items:center;
    margin-top:10px;
}

#dictionary #search-results{
    background-color: white;
    border-radius:8px;
    padding:15px;
}

#dictionary #search-results #word, #dictionary #search-results #pronunciation,#dictionary .def-box .type,#dictionary .def-box .definition,#dictionary .def-box .example{
    display:block;
    margin-bottom:5px;
}

#dictionary #search-results #top{
    margin-bottom:15px;
}

#dictionary #search-results #top #word{
    font-size:1.2rem;
    color:black;
}

#dictionary #search-results #top #pronunciation{
    font-size:0.8rem;
}

#dictionary .def-box{
    background-color:#F4F6F7;
    border-radius:8px;
    padding:10px;
    margin-top:10px;
}

#dictionary .def-box .type{
    font-size:0.8rem;
    color:black;
}

#dictionary .def-box .definition{
    font-size:1rem;
    margin:15px 0;
}


#dictionary .def-box .example{
    font-weight: normal;
    font-style:italic;
    font-size:0.9rem;
    color:#2E4053;
}
#fact-box{
    margin-bottom:1rem;
    padding:20px;
    background-color:white;
    border-radius:8px;
    color:#4e7686;
    font-size:1.1rem;
}

#fact{
    display:block;
    margin-top:15px;
    color:black;
    background-color:white;
    font-size:1rem;
    border-radius:8px;
}
#quick-links{
    background-color:white;
    padding:20px;
    border-radius:8px;
    margin-top:1rem;
}
